import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    cartlist: localStorage.getItem('productCart') ? JSON.parse(localStorage.getItem('productCart')) : []
  },
  mutations: {
    addCartList (state, payload) {
      const i = state.cartlist.findIndex(item => {
        return item.name === payload.name
      })
      if (i !== -1) {
        state.cartlist[i].count++
      } else {
        state.cartlist.push(payload)
      }
      localStorage.setItem('productCart', JSON.stringify(state.cartlist))
    },
    subCartList (state, payload) {
      const i = state.cartlist.findIndex(item => {
        return item.name === payload.name
      })
      state.cartlist[i].count--
      localStorage.setItem('productCart', JSON.stringify(state.cartlist))
    },
    delCartList (state, payload) {
      state.cartlist.splice(payload, 1)
      localStorage.setItem('productCart', JSON.stringify(state.cartlist))
      if (state.cartlist.length < 1) {
        localStorage.removeItem('productCart')
      }
    },
    submitCarlist (state) {
      localStorage.removeItem('productCart')
      state.cartlist = []
    }
  },
  actions: {
  },
  modules: {
  }
})
